@import url(https://fonts.googleapis.com/css2?family=Darker+Grotesque:wght@400;500;800&family=Lato&display=swap);
.header {
    width: 100%;
    height: 85px;
    background-color: #61DBFB;
    color: #212738;
    font-size: 54px;
    text-align: center;
    border-radius: 0 0 15px 15px;
}
.bg-color {
    background-color: #212738;
}

.filterbarContainer {
    margin: 0 auto;
    text-align: center;
    color: #212738;
    background-color: #61DBFB;
    height: 50px;
    border-radius: 10px;
    width: 80%;
}

.searchBar {
    background-color: #212738;
    height: 26px;
    width: 200px;
    border-radius: 10px;
    padding: 5px 10px;
    border: none;
    color: #f8f8f8;
    margin: 7px 10px;
    float: left;
}

.searchBar:focus {
    outline: none;
}

.button-group {
    border-radius: 10px;
    border: none;
    display: inline-block;
}

.btn {
    background-color: #212738;
    color: #61DBFB;
    border: none;
    border-radius: 10px;
    height: 100%;
    padding: 10px;
    margin: 7px 0;
}

.btn:focus {
    outline: none;
}
.wrapper {
    padding-top: 25px;
    height: 100%;
    display: flex;
    flex-flow: row wrap;
    justify-content: space-around;
    align-content: flex-start;
    overflow: auto;
    background-color: #212738;
    width: 80%;
    margin: 0 auto;
}

.card-container {
    background-color: #61DBFB;
    border-radius: 10px;
    box-shadow: 0px 4px 20px 0px rgba(0, 0, 0, 0.15);
    margin-bottom: 20px;
    width: 265px;
}

.card-intro {
    text-align: center;
    padding: 50px 10px 20px;
    color: #212738;
}

hr {
    border-top: 3px solid rgba(0, 0, 0, 0.1);
}

.card-contact {
    padding: 15px 0 20px;
}

li {
    color: #212738;
    list-style: none;
    margin-bottom: 15px;
}

a {
    padding-left: 10px;
    color: #212738;
}

img {
    border-radius: 50%;
}

body {
    margin: 0;
    font-family: "Darker Grotesque", -apple-system, BlinkMacSystemFont,
        avenir next, avenir, helvetica neue, helvetica, Ubuntu, roboto, noto,
        segoe ui, arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    background-color: #212738;
}

/* Typography */
h1 {
    font-size: 56px;
    font-weight: 400;
    margin-top: 30px;
}

h2 {
    font-size: 28px;
    font-weight: 500;
    margin-top: 20px;
    text-transform: capitalize;
}

h3 {
    font-size: 16px;
    font-weight: 800;
    letter-spacing: 2px;
    text-transform: uppercase;
}

h4 {
    font-family: "lato", -apple-system, BlinkMacSystemFont, avenir next, avenir,
        helvetica neue, helvetica, Ubuntu, roboto, noto, segoe ui, arial,
        sans-serif;
    font-size: 18px;
    /* font-style: italic; */
    font-weight: 400;
    color: #dad524;
    border: 1px rgba(218, 213, 36, 0.5) solid;
    text-align: center;
    padding: 20px;
    margin: 30px;
}

p,
a {
    font-family: "lato", -apple-system, BlinkMacSystemFont, avenir next, avenir,
        helvetica neue, helvetica, Ubuntu, roboto, noto, segoe ui, arial,
        sans-serif;
    font-size: 14px;
    font-weight: 400;
}

a:hover {
    text-decoration: none;
    color: #f8f8f8;
    transition: ease 0.5s;
}

/* Colors */
.bg-color {
    background-color: #212738;
}
.bg-lighter-color {
    background-color: #212738;
}
.bg-lightest-color {
    background-color: #212738;
}
.text-color {
    color: #212738;
}
.interactive-color {
    color: #f8f8f8;
}

/* —————————————————————————————————————————————
MEDIA QUERIES 
————————————————————————————————————————————— */
@media only screen and (max-width: 768px) {
    h1 {
        font-size: 46px;
    }
}

